import { BrowserRouter as Router, Link, Switch, Route } from 'react-router-dom';
import List from './components/List';
import vobs from './data';

function App() {
  return (
    <Router>
      <nav>
        <div className="links">
          <Link to="/static">Voby statyczne</Link>
          <Link to="/nature">Voby natury</Link>
          <Link to="/effects">Efekty wizualne</Link>
          <Link to="/sounds">Efekty dźwiękowe</Link>
          <Link to="/">Wszystkie voby</Link>
        </div>
      </nav>
      <Switch>
        <Route path="/static">
          <List data={vobs.static} />
        </Route>
        <Route path="/nature">
          <List data={vobs.nature} />
        </Route>
        <Route path="/effects">
          <List data={vobs.effects} />
        </Route>
        <Route path="/sounds">
          <List data={vobs.sounds} />
        </Route>
        <Route exact path="/">
          <List data={vobs.all} />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
